@import "https://fonts.googleapis.com/css2?family=Alkatra:wght@600&family=Wix+Madefor+Display:wght@400;500;600;700&display=swap";
:root {
  --font-title: "Alkatra", sans-serif;
  --font-body: "Wix Madefor Display", sans-serif;
  --clr-primary: #111016;
  --clr-secondary: #211f2b;
  --clr-accent: #8c809d;
  --clr-white: #fbf6ff;
  --clr-black: #0b0a0f;
  --clr-disabled: #9c9c9c;
  --border: solid 2px var(--clr-black);
}

:before, :after, * {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-body);
  background-color: var(--clr-primary);
  color: var(--clr-white);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  display: flex;
}

nav > ul {
  gap: 2rem;
  list-style: none;
  display: flex;
}

nav > ul > li > a {
  color: var(--clr-white);
  text-decoration: none;
}

video {
  background-color: var(--clr-black);
}

a, button {
  font-family: var(--font-body);
  color: var(--clr-black);
}

.preview-container {
  background-color: var(--clr-secondary);
  border: var(--border);
  border-radius: 1rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  padding: 2rem;
  display: flex;
}

.btn-container {
  justify-content: space-evenly;
  gap: 1rem;
  width: 100%;
  display: flex;
  position: relative;
}

.btn-container button, .btn-container a {
  background-color: var(--clr-accent);
  cursor: pointer;
  border: none;
  border: var(--border);
  border-radius: 10rem;
  padding: .5rem 1rem;
  font-size: 1rem;
  font-weight: 600;
}

button:disabled, .download-menu a[aria-disabled="true"] {
  background-color: var(--clr-disabled);
  cursor: not-allowed;
}

.download-container:hover > #btn-download-more:enabled ~ .download-menu {
  display: flex;
}

.download-menu {
  background-color: var(--clr-secondary);
  border: var(--border);
  border-radius: 1rem;
  flex-direction: column;
  gap: 1rem;
  padding: 1rem;
  display: none;
  position: absolute;
}

.download-menu a {
  width: max-content;
}

.text-container {
  flex-direction: column;
  gap: 1rem;
  display: flex;
}

.text-container > p {
  max-width: 56ch;
  line-height: 1.4;
}

footer {
  text-align: center;
  background-color: var(--clr-secondary);
  width: 100%;
  padding: 2rem;
}

footer a {
  color: var(--clr-white);
}

